import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { LockOutlined, MailOutlined } from '@ant-design/icons';
import { Button, Form, Input, Alert } from "antd";
import { signUp, showAuthMessage, showLoading, hideAuthMessage } from 'redux/actions/Auth';
import { useHistory } from "react-router-dom";
import { motion, useSpring } from "framer-motion"
import axios from 'axios';
import { Typography, message as msg } from 'antd';
import { Content } from 'antd/lib/layout/layout';



export const RegisterForm = (props) => {
	const[registerInput , setRegister] = useState();

const [state, setState] = useState({
	name:''
})

const [errors, setErros] = useState({
	errors:''
});

const registerSubmit = (val) => {
	//e.preventDefault();



	const data = {
		name: val.name,
		email: val.email,
		password: val.password,
	}
	
	axios.get('http://lara.localhost/api/sanctum/csrf-cookie').then(response => {
		axios.post('http://lara.localhost/api/register', data).then(res => {
			//showLoading();
			if(res.data.status === "success")
			{
				history.push("http://localhost:3000/app/pages/user-list");
				//setRegister(res.data.user);
				localStorage.setItem('auth_token', res.data.token);
				localStorage.setItem('auth_name', res.data.username);
			}
			else if(res.data.status ==="failed")
			{
				//console.log(res.data.datas[0].email)
				setErros(JSON.stringify(res))
			}

		})
	})
	
}

//const err = JSON.stringify(errors);
console.log(errors)




var rules = {
	email: [
		{ 
			required: true,
			message: 'Please input your email address',
			emailmessage: errors.email,

		},
		{ 
			type: 'email',
			message: 'Please enter a validate email!'
		}
	],
	password: [
		{ 
			required: true,
			message: 'Please input your password',
			passmessage: errors.password,

		}
	],
	name: [
		{ 
			message: errors.name,
		}
	],
	confirm: [
		{ 
			required: false,
			message: 'Please confirm your password!'
		},
		({ getFieldValue }) => ({
			validator(rule, value) {
				if (!value || getFieldValue('password') === value) {
					return Promise.resolve();
				}
				return Promise.reject('Passwords do not match!');
			},
		})
	]
}

rules.email[0].message = errors.email;
const error = errors.email;
console.log(error);


const { Text, Link } = Typography;


const { signUp, showLoading, token, loading, redirect, message, showMessage, hideAuthMessage, allowRedirect } = props

	const [form] = Form.useForm();
	let history = useHistory();
	const onSignUp = () => {
		console.log(errors)
    	form.validateFields().then(values => {
			showLoading()
			console.log(values);
			form.setFields([
				{
				  name: 'name',
				  errors: ["Please Enter Name"],
				},
			]);
			registerSubmit(values);
		}).catch(info => {
			console.log('Validate Failed:', info);
		});
	}

	useEffect(() => {
		if (token !== null && allowRedirect) {
			history.push(redirect)
		}
		if(showMessage) {
			setTimeout(() => {
				hideAuthMessage();
			}, 2000);
		}
	});
	

	return (
		<>
			<motion.div 
				initial={{ opacity: 0, marginBottom: 0 }} 
				animate={{ 
					opacity: showMessage ? 1 : 0,
					marginBottom: showMessage ? 20 : 0 
				}}> 
				<Alert type="error" showIcon message={message}></Alert>
			</motion.div>
			<Form form={form} layout="vertical" name="register-form" onFinish={onSignUp}>
				<Form.Item 
					name="email" 
					label="Email" 
					rules={rules.email}
					hasFeedback
				>
					<Input prefix={<MailOutlined className="text-primary" />}/>
				</Form.Item>
				<Form.Item 
					name="name" 
					label="Name" 
					rules={rules.name}
					hasFeedback
				>
					<Input  prefix={<MailOutlined className="text-primary" />}/>
				</Form.Item>
				<Text type="danger">{errors.password}</Text>
				<Form.Item 
					name="password" 
					label="Password" 
					rules={rules.password}
					hasFeedback
				>
					<Input.Password prefix={<LockOutlined className="text-primary" />}/>
				</Form.Item>
				
				<Form.Item>
					<Button type="primary" htmlType="submit" block loading={loading}>
						Sign Up
					</Button>
				</Form.Item>
			</Form>
		</>
	)
}

const mapStateToProps = ({auth}) => {
	const { loading, message, showMessage, token, redirect } = auth;
  return { loading, message, showMessage, token, redirect }
}

const mapDispatchToProps = {
	signUp,
	showAuthMessage,
	hideAuthMessage,
	showLoading
}


export default connect(mapStateToProps, mapDispatchToProps)(RegisterForm)
